<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "App",
  computed: {
    ...mapGetters(["githubUsername", "htmlTitle"])
  },
  created() {
    this.$store.dispatch("Init");
    this.$store.dispatch("GetInfo");
    this.$setTitle(this.$route.meta.title);
    let windowSize = this.$util.getWindowSize();
    let pathArr = this.$route.path.split("/");
    if (pathArr[1] == "user" && windowSize.height > windowSize.width * 1.2) {
      this.$router.push("/mobile/user/blog");
    }
    if (pathArr[1] == "mobile" && windowSize.height <= windowSize.width * 1.2) {
      this.$router.push("/");
    }
  }
};
</script>
<style lang="less">
.markdown-body {
  // Since element-ui and vint-ui will set list-style to be none globally. So we override the markdown list-style here
  ul {
    list-style-type: disc;
  }
  ol {
    list-style-type: decimal;
  }
  ul ul,
  ol ul {
    list-style-type: circle;
    margin-left: 15px;
  }
  ol ol,
  ul ol {
    list-style-type: lower-latin;
    margin-left: 15px;
  }
  .v-note-wrapper {
    .v-note-panel {
      box-shadow: none;
    }
  }
}
</style>



